import { mapState, mapGetters } from 'vuex';
export default {
	data() {
		return {
			personinfo: {},
			downOption: {
				auto: false //是否在初始化后,自动执行下拉回调callback; 默认true
			},
			upOption: {
				auto: false
			},
			work_detail: [],
			loading: true,
			wages: ''
		};
	},
	onLoad(option) {
		this.personinfo = JSON.parse(decodeURIComponent(option.item));
		uni.$on('refreshList', () => {
			console.log('refreshList');
			this.init();
		});
		this.init();
	},
	onUnload() {
		uni.$off('refreshJwt');
	},
	computed: {
		c_CustomBar() {
			return this.CustomBar;
		},
		...mapGetters(['id']),
		no_data() {
			return !this.loading && this.work_detail.length == 0;
		},
		sum_work_tiem(){
			let time = 0
			this.work_detail.forEach(item =>{
				time += this.$_.toNumber(item.workTime)
			})
			return time
		},
		sum_price(){
			return this.sum_work_tiem * this.$_.toNumber(this.wages)
		}
	},
	methods: {
		init() {
			this.get_work_time();
		},
		downCallback(mescroll) {
			// 这里加载你想下拉刷新的数据, 比如刷新轮播数据
			// loadSwiper();
			// 下拉刷新的回调,默认重置上拉加载列表为第一页 (自动执行 mescroll.num=1, 再触发upCallback方法 )
			mescroll.resetUpScroll();
		},
		/*上拉加载的回调: mescroll携带page的参数, 其中num:当前页 从1开始, size:每页数据条数,默认10 */
		upCallback(mescroll) {
			//联网加载数据
			mescroll.endErr();
			// setTimeout(() => {
			// 	mescroll.endErr();
			// }, 1000);
		},
		get_work_time() {
			this.$http
				.get('personwx.personproworktimedetail/1.0/', {
					pid: this.id
				})
				.then(res => {
					console.log(res);
					this.work_detail = res.data.data;
				})
				.finally(() => {
					this.loading = false;
				});
		},
		back() {
			uni.navigateBack({
				delta: 1
			});
		},
		sub_settle(){
			uni.showModal({
			    title: '提示',
			    content: '当前结算金额为1280元，确认是否结算',
				'confirmText':'确认结算',
			    success:(res) => {
			        if (res.confirm) {
			            console.log('用户点击确定');
						this.$http.post('personwx.personproworktimeseet/1.0/',{
							pid:this.id,
							proId:this.personinfo.id,
							wages:this.wages
						}).then(res =>{
							console.log(res)
							this.back()
						})
			        } else if (res.cancel) {
			            console.log('用户点击取消');
			        }
			    }
			});
		}
	}
};
